*{
    margin: 0;
    padding: 0;
}

.playlistMusicBox-wrap{
    width: 100%;
    height: 60px;
    background-color: rgb(215, 175, 135);
    border-bottom: 1px solid gray;

    display: flex;
    align-items: center;

    padding: 5px;
    box-sizing: border-box;
}

.playlistMusicBox-wrap>#img-area>img {
    width: 50px;
    height: 50px;
    float: left;
}

.playlistMusicBox-wrap>#left-area{
    float: left;
}

.playlistMusicBox-wrap>#left-area>span{
    display: block;
    float: left;
    width: 290px;
    height: 20px;
    padding-left: 30px;
    box-sizing: border-box;
    background-color: pink;
}

.playlistMusicBox-wrap>#right-area>span{
    display: block;
    width: 60px;
    height: 20px;
    background-color: maroon;
    float: right;
}