.PlayMusicCommentArea{
    width: 450px;
    height: 560px;
    background-color: lightgreen;

    float: left;
}

.PlayMusicCommentArea>#commentRead{
    width: 100%;
    height: 500px;
    background-color: aliceblue;
    overflow: auto;
}

.PlayMusicCommentArea>#commentInput{
    width: 100%;
    height: 60px;
    background-color: brown;
    padding: 15px;
    box-sizing: border-box;
}

.PlayMusicCommentArea>#commentInput>input{
    width: 350px;
    height: 25px;
    margin-right: 10px;
}

.PlayMusicCommentArea>#commentInput>button{
    width: 50px;
    height: 25px;
}